import React from 'react';
import { useParams } from 'react-router-dom';
import Main from '../layouts/Main';
import data from '../data/projects';
import './ProjectDetails.css';

const ProjectDetails = () => {
  const { id } = useParams();
  const project = data.find((project) => project.id === id);

  if (!project) {
    return <div>Project not found</div>;
  }

  const renderProjectContent = () => {
    switch (project.id) {
      case 'nearest-dollar':
        return (
          <>
            <p>{project.desc}</p>
            <img className="project-image" src="/images/projects/nearestdollar-demo.gif" alt="Nearest Dollar Demo" />
            <p>Nearest Dollar was a great learning experience in building a full-stack application with real-time bank account integration. It taught me a lot about securely handling sensitive financial data and building a user-friendly interface for charitable giving.</p>
          </>
        );
      case 'harvest':
        return (
          <>
            <p>{project.desc}</p>
            <div className="image-grid">
              <img className="project-image" src="/images/projects/harvest-field.jpg" alt="Harvest Field" />
              <img className="project-image" src="/images/projects/harvest-dashboard.jpg" alt="Harvest Dashboard" />
            </div>
            <p>Winning the Techcrunch Disrupt SF Hackathon with Harvest was an incredible experience. It validated our idea and gave us the motivation to continue working on the project beyond the hackathon. The prize money also helped us fund the initial development and testing of the product.</p>
          </>
        );
      case 'space-potato':
        return (
          <>
            <p>{project.desc}</p>
            <div className="video-container">
              <iframe
                width="100%"
                height="315"
                src="https://www.youtube.com/embed/abc123"
                title="Space Potato Launch Video"
                frameBorder="0"
                allowFullScreen
              ></iframe>
            </div>
            <p>Launching Space Potato was a childhood dream come true. It taught me a lot about aerospace engineering, project management, and the power of crowdfunding. The coffee table book we published with the photos from the launch was a great way to share the experience with our backers and supporters.</p>
          </>
        );
      case 'cat-detector':
        return (
          <>
            <p>{project.desc}</p>
            <img className="project-image" src="/images/projects/catdetector-diagram.jpg" alt="Cat Detector Diagram" />
            <p>Building the Cat Detector taught me a lot about machine learning and convolutional neural networks. It was also a fun way to combine my love for cats with my passion for technology. Although the server costs eventually made the project unsustainable, it was a great learning experience and a fun talking point with fellow cat lovers.</p>
          </>
        );
        case 'NBA-Dataset':
          return (
            <>
              <p>{project.desc}</p>
              <p>Our reserach questions were: would age affect the performance of a NBA player? would the dominant NBA stars significantly affect the overall performace of their teams? and how can we better visualize the performance of an individual player?</p>
              <p>We have also made a website with R, and it can be accessed <a href="https://miked232.shinyapps.io/final-project-info201/">here</a>, and here is a snapshot below.</p>
              <img className="project-image" src="/images/projects/NBA-snapshot.png" alt="NBA Dataset website snapshot" />
            </>
          );
          case 'story-tree':
            return (
              <>
                <p>{project.desc}</p>
                <p>What is Storytree? Storytree is a collaborative writing platform allowing for people to write stories together. Create the first chapter of a story and have others build off of it in future chapters. Then browse the creations built upon by other people. Want to see a different interpretation? Look at different paths and upvote your favorite ones. Don't like the direction of a story? Branch it off and write your own ending!</p>
                <p>Used Javascript, React, Fuse.io to complete this website. </p>
                <p>You can visit the website <a href="https://storytree-5fd56.web.app/">here</a>.</p>
                <p>Here's a snapshot of the website:</p>
                <img className="project-image" src="/images/projects/storytree-snap.png" alt="Storytree website snapshot" />
              </>
            );
      default:
        return null;
    }
  };

  return (
    <Main>
      <article className="portfolio-details">
        <div className="title-container">
          <h1>{project.title}</h1>
          <span>{project.subtitle}</span>
        </div>
        <div className="project-info">
          <div className="image-container">
            <img className="project-image" src={project.image} alt={project.title} />
          </div>
          <div className="details-container">{renderProjectContent()}</div>
        </div>
      </article>
    </Main>
  );
};

export default ProjectDetails;